.image-upload button {
  background-color: skyblue;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;

 
  
}

.image-upload button:hover {
  background-color: tomato;
}

.main-container {
  height: 100vh;
  background: linear-gradient(to bottom, #4462c9, #a852f4);
  align-items: center;
  color: white;
  text-shadow: none;
  justify-content: center;
  text-align: center;
  display: flex;
  flex-direction: column;
 
}

.image-upload {
    border: 1px solid black;
    border-radius: 8px; 
    padding: 20px; 
    text-align: center; 
    background-color: white; 
    padding-top: 40px;
    width: 300px;
    margin-left: 20px;
    justify-content: space-between;
    display: flex;
    
    
  }
 
  .image-upload-input{
     
        
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
  }

  .image-info{
    border: 1px solid black;
    border-radius: 8px; 
    padding: 20px; 
    text-align: center; 
    background-color: black; 
    padding-top: 40px;
    width: 400px;
    height: 300px;
    margin-left: 20px;
  }
  .extract{
    height: 200px;
    text-align: justify;
    width: 400px;
    overflow: scroll;
  }